import React from 'react';
import { useHistory } from 'react-router-dom';
import * as S from "./err.style.js"

const Error = () => {

    // 
    const history = useHistory();
    const onCancel = () =>{
        history.goBack();
    }

    return (
        <S.ErrContent>
            <S.ErrTit>OOPS!</S.ErrTit>
            <S.ErrTxt>  .</S.ErrTxt>
            <S.ErrBackBtn type='button' className='btn_err' onClick={onCancel}></S.ErrBackBtn>
        </S.ErrContent>
    );
};

export default Error;